import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Card, Grid, Button, Loader, Header } from 'semantic-ui-react';
import {apiCall} from "../services/api";

export default class ProjectsGrid extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      projects: [],
    };

    this.onNewProject = this.onNewProject.bind(this);
  }

  async componentDidMount() {
    try {
      const projects = await apiCall("get",`${process.env.REACT_APP_PRODUCTION_SERVER_URL}/api/projects/`);
      // console.log("projects",projects);
      if (projects.error && projects.status === 401) {
        window.location = '/#/admin/login/';
        return;
      }

      this.setState({
        isLoaded: true,
        projects,
      });
    } catch (error) {
      this.setState({
        isLoaded: true,
        error,
      });
      window.location = '/#/admin/login/';
    }
  }

  async onNewProject() {
    const newProject = await apiCall("post",`${process.env.REACT_APP_PRODUCTION_SERVER_URL}/api/projects`);
    this.setState({
      projects: this.state.projects.concat([newProject]),
    });
  }

  render() {
    const { error, isLoaded, projects } = this.state;
    const { linkPrefix, newButton, title } = this.props;

    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <Loader active inline="centered" />;
    }

    const renderProjectCard = project => {
      const { name, form, imagesCount, labelsCount } = project;
      const id = project._id;
      const info = `${imagesCount} images, ${labelsCount} labeled`;
      const desc = `Tags: ${form.formParts.map(part => part.name).join(', ')}`;
      return (
        <Grid.Column key={id}>
          <a href={`/#${linkPrefix}${id}`}>
            <Card fluid link header={name} meta={info} description={desc} />
          </a>
        </Grid.Column>
      );
    };

    const renderedButton = newButton ? (
      <Button style={{ padding: '1.5em' }} onClick={this.onNewProject}>
        <span style={{ fontSize: '4em' }}>+</span>
        <div>Make a new project</div>
      </Button>
    ) : null;

    return (
      <div>
        <Header as="h1">{title}</Header>
        <Grid stackable columns={2}>
          {projects.map(renderProjectCard)}
          <Grid.Column>{renderedButton}</Grid.Column>
        </Grid>
      </div>
    );
  }
}
