import React, { Component, Fragment} from 'react';

import Menubar from './Menubar';
import ProjectsGrid from './ProjectsGrid';

export default class LabelHome extends Component {
  render() {
    let backgroundImg = `linear-gradient(45deg, rgb(229, 44, 78) 1%, rgb(233, 74, 99) 30%, rgb(174, 82, 157) 69%, rgb(154, 85, 177) 100%)`;
    return (
      <Fragment>
        <header className="App-hero" style={{
            "minHeight":"75px",
            "backgroundImage":backgroundImg,
            "color":"white"
          }}>
          </header>
        <div>  
          <Menubar {...this.props} active="label">
            <ProjectsGrid
              {...this.props}
              title="Start Labeling:"
              linkPrefix="/label/"
              newButton={false}
            />
          </Menubar>
        </div>
      </Fragment>
    );
  }
}
