import React, { Component, Fragment } from 'react';
import {  HashRouter as Router, Switch, Route } from 'react-router-dom';

import '../styles/AdminApp.css';
import Menubar from './Menubar';
import ProjectsGrid from './ProjectsGrid';
import ProjectPage from './ProjectPage';
import LoginPage from './LoginPage';

class AdminApp extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    let backgroundImg = `linear-gradient(45deg, rgb(229, 44, 78) 1%, rgb(233, 74, 99) 30%, rgb(174, 82, 157) 69%, rgb(154, 85, 177) 100%)`;
    return (
    <Fragment>
      <header className="App-hero" style={{
            "minHeight":"75px",
            "backgroundImage":backgroundImg,
            "color":"white"
          }}>
          </header>
        <div>  
          <Menubar active="admin">
              <Router>
                <Switch>
                 <Route path="/admin/:projectId"
                          render={
                            props=>{
                              return (
                              <ProjectPage browserLang = {this.props.browserLang} {...this.props}></ProjectPage> 
                            )}
                           }
                        />
                  <Route
                    exact
                    path="/admin/"
                    render={() => (
                      <ProjectsGrid
                        linkPrefix="/admin/"
                        title="Edit Projects:"
                        newButton={true}
                      />
                    )}
                  />
                  <Route exact path="/admin/login" 
                          render={
                            props=>{
                              return (
                              <LoginPage browserLang = {this.props.browserLang} {...this.props}></LoginPage> 
                            )}
                           }
                          />
                </Switch>
              </Router>
          </Menubar>
        </div>
      </Fragment>
    );
  }
}

export default AdminApp;
